.btn {
  text-transform: uppercase;
  line-height: px(22); // Correct vertical text centering

  &.btn-primary {
    color: $white;
    border-color: theme-color("primary");
  }

  &.btn-secondary {
    @extend .btn-outline-primary;
  }

  &.btn-default {
    @extend .btn-outline-default;
  }

  &.btn-link {
    color: $link-color;
    border-color: transparent;
  }
}
